import axios from 'axios'
import Vue from 'vue'

// 
axios.defaults.withCredentials = true
axios.defaults.timeout = 5000
const httpLogin = axios.create({
  baseURL: 'http://192.168.31.217:4000/'
})
const httpMusic = axios.create({
  baseURL: 'http://192.168.31.217:3000/'
})

//  count 
let count = 0

// 
httpMusic.interceptors.request.use(function (config) {
  // 
  count++
  Vue.showLoading()
  return config
}, function (error) {
  // 
  Vue.hiddenLoading()
  return Promise.reject(error)
})

// 
httpMusic.interceptors.response.use(function (response) {
  // 
  count--
  if (count === 0) {
    Vue.hiddenLoading()
  }
  return response
}, function (error) {
  // 
  Vue.hiddenLoading()
  return Promise.reject(error)
})

// 
export default {
  // get
  get: function (path = '', data = {}) {
    return new Promise(function (resolve, reject) {
      httpMusic.get(path, {
        params: data
      }).then(function (response) {
        resolve(response.data)
      }).catch(function (error) {
        reject(error)
      })
    })
  },

  // post
  post: function (path = '', data = {}) {
    return new Promise(function (resolve, reject) {
      httpLogin.post(path, data).then(function (response) {
        resolve(response.data)
      }).catch(function (error) {
        reject(error)
      })
    })
  },

  // all
  all: function (list) {
    return new Promise((resolve, reject) => {
      // axiosaxios
      axios.all(list)
        .then(axios.spread((...result) => {
          // 
          resolve(result)
        }))
        .catch((err) => {
          reject(err)
        })
    })
  }
}
